import logo from './logo.svg';
import './App.css';
import WebApp from "@twa-dev/sdk";
import {useState} from "react";
import {useWeb3React} from "@web3-react/core";
import {InjectedConnector} from '@web3-react/injected-connector';
const injected = new InjectedConnector();

function App() {
  const [count, setCount] = useState(0)

  const {connector, library, chainId, account, active, error, activate, deactivate} = useWeb3React();

  const connectWallet = async () => {
     try {
         await activate(injected, (error) => {
             // 크롬 익스텐션 없을 경우 오류 핸들링
             if ('/No Ethereum provider was found on window.ethereum/')
                 throw new Error('Metamask 익스텐션을 설치해주세요');
            });
        } catch (err) {
            alert(err);
            window.open('https://metamask.io/download.html');
        }
    };

  // https://github.com/twa-dev/SDK/blob/45b54230902e36f4deeaaeeb140559008e82d0f4/src/telegram-web-apps.js#L1574
  return (
      <>
          <div className="card">
              <button onClick={() => WebApp.expand()}>
                  expand
              </button>
              <button onClick={() => WebApp.close()}>
                  close
              </button>
          </div>
          <div className="card">
              <button onClick={() => connectWallet()}>
                  ConnectWallet
              </button>
          </div>
          <div className="App">
              <header className="App-header">
                  <img src={logo} className="App-logo" alt="logo"/>
                  <p>
                      Edit <code>src/App.js</code> and save to reload.
                  </p>
                  <a
                      className="App-link"
                      href="https://reactjs.org"
                      target="_blank"
                      rel="noopener noreferrer"
                  >
                      Learn React
                  </a>
              </header>
          </div>
          <div className="card">
              <button onClick={() => setCount((count) => count + 1)}>
                  count is {count}
              </button>
          </div>
          {/* Here we add our button with alert callback */
          }
          <div className="card">
              <button onClick={() => WebApp.showAlert(`Current count is ${count}`)}>
                  ShowAlert
              </button>
              <button onClick={() => WebApp.showPopup({'title': 'test_title', 'message': 'this is a message'})}>
                  ShowPopup
              </button>
              <button onClick={() => WebApp.showConfirm(`Current count is ${count}`)}>
                  ShowConfirm
              </button>
              <button onClick={() => WebApp.showScanQrPopup({'text': 'test_title'})}>
                  showScanQrPopup
              </button>
          </div>
          <div className="card">
              <button onClick={() => WebApp.openLink('https://www.myailey.com')}>
                  openLink
              </button>
          </div>
          <div className="card">
              <button onClick={() => WebApp.requestWriteAccess()}>
                  requestWriteAccess
              </button>
              <button onClick={() => WebApp.requestContact()}>
                  requestContact
              </button>
          </div>
      </>);
}

export default App;
